import * as utils from '../utils/util';
import AbstractView from '../framework/view/abstract-view';

function createOffersTemplate(offers) {
  return offers.map(({title, price}) => `<li className="event__offer">
      <span className="event__offer-title">${title}</span>
      &plus;&euro;&nbsp;
      <span className="event__offer-price">${price}</span>
    </li>`).join('');
}

function createPointTemplate({point, type, destination, offers}) {
  const {basePrice, dateFrom, dateTo, isFavorite} = point;
  const dateFromConvert = utils.getDate(dateFrom);
  const dateToConvert = utils.getDate(dateTo);
  const isFavoriteCheck = isFavorite ? 'event__favorite-btn--active' : '';
  const pointOffers = offers.filter((obj) => point.offers.some((pointOffer) => obj.id === pointOffer));

  return `<li class="trip-events__item">
              <div class="event">
                <time class="event__date" datetime=${dateFrom}>${utils.getShortDate(dateFromConvert)}</time>
                <div class="event__type">
                  <img class="event__type-icon" width="42" height="42" src=${type.image} alt="Event type icon">
                </div>
                <h3 class="event__title">${destination.name}</h3>
                <div class="event__schedule">
                  <p class="event__time">
                    <time class="event__start-time" datetime=${dateFrom}>${utils.getShortTime(dateFromConvert)}</time>
                    &mdash;
                    <time class="event__end-time" datetime=${dateTo}>${utils.getShortTime(dateToConvert)}</time>
                  </p>
                  <p class="event__duration">${utils.getTimeDifference(dateFromConvert, dateToConvert)}</p>
                </div>
                <p class="event__price">
                  &euro;&nbsp;<span class="event__price-value">${basePrice}</span>
                </p>
                <h4 class="visually-hidden">Offers:</h4>
                <ul class="event__selected-offers">
                ${createOffersTemplate(pointOffers)}
                </ul>
                <button class="event__favorite-btn ${isFavoriteCheck}" type="button">
                  <span class="visually-hidden">Add to favorite</span>
                  <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
                    <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
                  </svg>
                </button>
                <button class="event__rollup-btn" type="button">
                  <span class="visually-hidden">Open event</span>
                </button>
              </div>
            </li>`;
}

export default class PointView extends AbstractView{

  #point;
  #type;
  #destination;
  #offers;
  #handleEditClick;
  #handleFavoritesClick;

  constructor({point, type, destination, offers, onEditClick, onFavoritesClick}) {
    super();
    this.#point = point;
    this.#type = type;
    this.#destination = destination;
    this.#handleEditClick = onEditClick;
    this.#handleFavoritesClick = onFavoritesClick;
    this.#offers = offers;
    this.element.querySelector('.event__rollup-btn').addEventListener('click', this.#editClickHandler);
    this.element.querySelector('.event__favorite-btn').addEventListener('click', this.#favoritesClickHandler);
  }

  get template() {
    return createPointTemplate({point: this.#point, type: this.#type, destination: this.#destination, offers: this.#offers});
  }

  #editClickHandler = (evt) => {
    evt.preventDefault();
    this.#handleEditClick();
  };

  #favoritesClickHandler = (evt) => {
    evt.preventDefault();
    this.#handleFavoritesClick();
  };
}
